/**
 * [1,1,2]  [1]
 * @param {*} origin 
 * @param {*} field 
 */
export const getDuplicateItem = (origin, field) => {
  return duplicate(origin, field, 'get');
};

/**
 * [1,1,2]  [1,2]
 * @param {*} origin 
 * @param {*} field 
 */
export const removeDuplicateItem = (origin, field) => {
  return duplicate(origin, field, 'remove');
};

/**
 * [1,1,2]  [2]
 * @param {*} origin 
 * @param {*} field 
 */
export const deleteDuplicateItem = (origin, field) => {
  return duplicate(origin, field, 'delete');
};

/**
 *  value 
 * @param {*} origin 
 * @param {*} filed 
 * @param {*} type : 'get': 'remove': 'delete': 
 */
function duplicate(origin, filed, type) {
  if (!filed) {
    return Array.from(new Set(origin));
  } else {
    if (type === 'delete') {
      let list = [];
      for (let i = 0; i < origin.length; i++) {
        for (let j = i + 1; j < origin.length; j++) {
          if (origin[j][filed] == origin[i][filed]) {
            list.push(i);
            list.push(j);
          }
        }
      }
      list = Array.from(new Set(list));
      list.forEach((v) => origin.splice(v, 1, null));
      return origin.filter(Boolean);
    } else {
      let list = []; // 
      for (let i = 0; i < origin.length; i++) {
        for (let j = i + 1; j < origin.length; j++) {
          if (origin[j][filed] == origin[i][filed]) {
            list.push(...origin.splice(j, 1));
            j--;
          }
        }
      }
      return type === 'get' ? list : origin;
    }
  }
}

/**
 * URL
 * @param {*} name
 * @returns
 */
export const GetQueryString = (name) => {
  const reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)');
  const matchStr = window.location.href.split('?')[1];
  if (matchStr) {
    const r = matchStr.match(reg);
    if (r !== null) return unescape(r[2]);
    return null;
  } else {
    return null;
  }
};

/**
 * 
 * @param data 
 * @returns false,true
 */
export const isNotEmpty = (data) => {
  if (data === null || data === undefined) {
    return false;
  }
  if (typeof data === 'string' && data.trim() === '') {
    return false;
  }
  if (Array.isArray(data) && data.length === 0) {
    return false;
  }
  if (Object.prototype.toString.call(data) === '[object Object]' && Object.keys(data).length === 0) {
    return false;
  }
  return true;
};

/**
 * 
 * @param data 
 * @returns true,false
 */
export const isEmpty = (data) => {
  return !isNotEmpty(data);
};

/**
 *
 * @param fn 
 * @param delay 
 * @returns 
 */
export function debounce(fn, delay) {
  let timer = null; //1
  return function (...args) {
    if (timer) clearTimeout(timer); //2
    timer = setTimeout(() => {
      fn.apply(this, args); //3setTimoutthisthisdebounce
    }, delay);
  };
}

/**
 * 
 * @param {*blob} blob
 * @param {*} filename
 */
export function downloadFile(blob, filename) {
  const a = document.createElement('a');
  const url = window.URL.createObjectURL(new Blob([blob]));
  const name = filename || '';
  a.href = url;
  a.download = name;
  a.click();
  window.URL.revokeObjectURL(url);
}

/**
 * 
 * @param date 
 * @param fmt 
 * @returns 
 */
export const dateFormat = (date, fmt) => {
  const o = {
    'M+': date.getMonth() + 1, // 
    'd+': date.getDate(), // 
    'h+': date.getHours(), // 
    'm+': date.getMinutes(), // 
    's+': date.getSeconds(), // 
    'q+': Math.floor((date.getMonth() + 3) / 3), // 
    S: date.getMilliseconds(), // 
  };
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length));
  }
  for (const k in o) {
    if (new RegExp('(' + k + ')').test(fmt)) {
      fmt = fmt.replace(RegExp.$1, RegExp.$1.length === 1 ? o[k] : ('00' + o[k]).substr(('' + o[k]).length));
    }
  }
  return fmt;
};
