import Vue from 'vue';
import dialog from './dialog.vue';
import { isNotEmpty } from '../../../utils/util';
export default (config) => {
  if (typeof config.content === 'object') {
    // no handler
  } else if (typeof config.content === 'function') {
    const content = config.content;
    config.content = {
      render(h) {
        return h('div', [content(h)]);
      },
    };
  } else {
    const content = config.content.toString();
    config.content = {
      render(h) {
        return h('div', content);
      },
    };
  }
  Vue.component('dialog-name', config.content);
  const props = {
    title: null,
    width: '50%',
    fullscreen: false,
    top: '15vh',
    modal: true,
    modalAppendToBody: true,
    appendToBody: false,
    lockScroll: true,
    customClass: null,
    closeOnClickModal: true,
    closeOnPressEscape: true,
    showClose: true,
    beforeClose: null,
    center: false,
    destroyOnClose: false,
    /* props */
    params: null, // 
    content: null, // dialog Stringnumber | Object | function render
    okText: '', // 
    cancelText: '', // 
    footer: true, //  footer ,  component :close;parmas
  };
  const originEvent = {
    open: config.open,
    opened: config.opened,
    close: config.close,
    closed: config.closed,
    onConfirm: config.onConfirm, //  dialog  this
    onCancel: config.onCancel //  dialog  this close
      ? config.onCancel
      : (inst) => {
          inst.close();
        },
  };

  for (const [key, value] of Object.entries(config)) {
    if (Object.prototype.hasOwnProperty.call(props, key)) {
      if (isNotEmpty(value)) {
        props[key] = value;
      }
    }
  }
  const ComponentConstructor = Vue.extend(dialog);
  const instance = new ComponentConstructor({
    propsData: {
      props: { ...props },
    },
  });
  instance.$on('onCancel', originEvent.onCancel);
  for (const [key, value] of Object.entries(originEvent)) {
    if (Object.prototype.hasOwnProperty.call(config, key)) {
      instance.$on(key, value);
    }
  }
  instance.$mount();
  document.body.appendChild(instance.$el);
  return {
    methods: {
      close: instance.close,
      closed: instance.closed,
      open: instance.open,
      opened: instance.opened,
      confirm: instance.confirm,
      cancel: instance.cancel,
    },
    props: instance.$options.propsData.props,
  };
};
